<template>
  <div class="list">
    <Header>{{ sex }}{{ $route.query.title }}榜</Header>
    <h3>{{ sex }}{{ $route.query.title }}</h3>

    <van-skeleton
      title
      avatar
      :row="3"
      :loading="loading"
      avatar-shape="square"
    >
      <ul class="content">
        <ListItem
          v-for="(item, index) in books"
          :key="index"
          :book="item"
        ></ListItem>
      </ul>
    </van-skeleton>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import ListItem from "@/components/ListItem.vue";

export default {
  name: "List",
  components: {
    ListItem,
    Header,
  },
  data() {
    return {
      books: [],
      loading: true,
    };
  },
  watch: {
    books() {
      this.loading = false
    },
  },
  computed: {
    sex() {
      if (this.$route.query.gender == "female") {
        return "女生";
      } else {
        return "男生";
      }
    },
  },
  created() {
    this.axios.get(`/rank/${this.$route.query.id}`).then((response) => {
      //   console.log(response.data.ranking.books);
      this.books = response.data.ranking.books;
    });
  },
};
</script>

<style lang="less" scoped>
.list {
  padding: 50px 0;
  border-radius: 4px;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
  .van-skeleton{
    height: calc(100vh - 143px);
  }
  h3 {
    border-left: 2px solid #2ae3cf;
    padding-left: 10px;
    margin: 10px 0 15px 0;
    line-height: 1em;
    font-size: 18px;
    font-weight: bold;
  }
  .check-all {
    margin-top: 5px;
    cursor: pointer;
    text-align: center;
    padding-bottom: 10px;
    span {
      color: crimson;
      font-size: 12px;
      margin-right: 5px;
      display: inline-block;
    }
    .van-icon {
      font-size: 14px;
      transform: translateY(3px);
    }
  }
}
</style>